export default {
  data(){
    return {
      msg:'',
      userInfo :{}
    }
  },
  created(){
    const userInfo = JSON.parse(localStorage.getItem("userInfo"))
    if (userInfo) {
      this.userInfo = userInfo
    }
  },
  methods:{
    goLogin(){
      this.$router.push({
        //
        path: '/SignIn'
      })
    },
    goPerSional(){
      this.$common.Skip(this, '/Personal')
    },
    GoSignin(){
      this.$common.Skip(this, '/Signin')
    }
  }
}


