import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '../views/home/index.vue'
//
import UserIndex from '../views/user/index/index.vue'
//
import SignIn from '../views/login/signin/index.vue'
//
import Register from '../views/login/register/index.vue'
//
import PwdSignIn from '../views/login/pwdsignin/index.vue'
//
import ReleaseIndex from '../views/release/index/index.vue'
//
import History from '../views/release/history/index.vue'
//
import Publish from '../views/release/publish/index.vue'
//
import Details from '../views/release/details/index.vue'
//
import SearchInfo from '../views/release/search/index.vue'
//LIST
import SearchList from '../views/release/searchlist/index.vue'
//
import MySelf from '../views/user/myself/index.vue'
//
import Personal from '../views/user/personal/index.vue'
//
import UpPwd  from '../views/user/uppwd/index.vue'
//
import Safety  from '../views/user/safety/index.vue'
//
import authentication from '../views/user/authentication/index.vue'
//
import friendCircleIndex from '../views/friendcircle/index/index.vue'
//
import voiceIndex from '../views/voice/index/index.vue'
//
import videoIndex from '../views/video/index/index.vue'



Vue.use(Router)

export default new Router({
  mode:'hash',
  routes: [
    {
      path: '/index',
      name: 'Home',
      component: Home,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:false,
        tabbarActive:0,
        scrollTop:true,
        keepAlive:true
      }
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:false,
        tabbarActive:0,
        scrollTop:true,
        keepAlive:true
      }
    },
    {
      path: '/UserIndex',
      name: 'UserIndex',
      component: UserIndex,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:true,
        tabbarActive:5
      }
    },
    {
      path: '/SignIn',
      name: 'SignIn',
      component: SignIn,
      meta:{
        title:'',
        tshow:false,
        bshow:false,
      }
    },
    {
      path: '/PwdSignIn',
      name: 'PwdSignIn',
      component: PwdSignIn,
      meta:{
        title:'',
        tshow:false,
        bshow:false,
      }
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
      }
    },
    {
      path: '/friendCircleIndex',
      name: 'friendCircleIndex',
      component: friendCircleIndex,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:true,
        tabbarActive:4
      }
    },
    {
      path: '/videoIndex',
      name: 'videoIndex',
      component: videoIndex,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:true,
        tabbarActive:2
      }
    },
    {
      path: '/voiceIndex',
      name: 'voiceIndex',
      component: voiceIndex,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:true,
        tabbarActive:3
      }
    },
    {
      path: '/SearchInfo',
      name: 'SearchInfo',
      component: SearchInfo,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:false,
        keepAlive:true,
      }
    },
    {
      path: '/SearchList',
      name: 'SearchList',
      component: SearchList,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:false,
        keepAlive:true,
      }
    },
    {
      path: '/ReleaseIndex',
      name: 'ReleaseIndex',
      component: ReleaseIndex,
      meta:{
        title:'',
        tshow:false,
        bshow:true,
        requireAuth:true,
        tabbarActive:1
      }
    },
    {
      path: '/History',
      name: 'History',
      component: History,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:true,
        keepAlive:true,
      }
    },
    {
      path: '/Publish',
      name: 'Publish',
      component: Publish,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:true
      }
    },
    {
      path: '/Details',
      name: 'Details',
      component: Details,
      meta:{
        title:'',
        tshow:false,
        bshow:false,
        requireAuth:false
      }
    },
    {
      path: '/MySelf',
      name: 'MySelf',
      component: MySelf,
      meta:{
        title:'',
        tshow:false,
        bshow:false,
        requireAuth:true
      }
    },
    {
      path: '/Personal',
      name: 'Personal',
      component: Personal,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:true
      }
    },
    {
      path: '/UpPwd',
      name: 'UpPwd',
      component: UpPwd,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:true
      }
    },
    {
      path: '/Safety',
      name: 'Safety',
      component: Safety,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:true
      }
    },
    {
      path: '/authentication',
      name: 'authentication',
      component: authentication,
      meta:{
        title:'',
        tshow:true,
        bshow:false,
        requireAuth:true
      }
    }
  ]
})



