//time1time2
export function count_time(time1, time2) {
    time1 = Date.parse(new Date(time1));
    time2 = Date.parse(new Date(time2));
    return Math.abs(parseInt((time2 - time1) / 1000 / 3600 / 24));
}

export function DateClculate(time) {
    var myData = new Date(); //
    var endTime = new Date(time) //
    var lefttime = (endTime.getTime() - myData.getTime()) / (24 * 60 * 60 * 1000); //-=(/=)
    //=24 1=60 1=60 1=1000
    lefttime = Math.ceil(lefttime) //

    var leftHMS = parseInt((endTime.getTime() - myData.getTime()) / 1000); //
    var ds = parseInt(leftHMS / (24 * 60 * 60)); //
    var xs = parseInt(leftHMS / (60 * 60) % 24); // 24
    var fz = parseInt(leftHMS / 60 % 60); //
    var mz = parseInt(leftHMS % 60); //

    //
    return ds + '' + xs + '' + fz + '' + mz + '';
}



//  
export function DateClculateA(times) {
    var datalist = times.split(':');
    var ddd = new Date();

    return ddd.getTime() + parseInt(datalist[0]) * 60 * 60 * 1000 + parseInt(datalist[1]) * 60 * 1000 + parseInt(datalist[2]) * 1000
}
//    time  1000
export function DateClculateB(time, number) {
    var myDate = new Date(time);
    return myDate.getTime() - number
}

