export function getMyDateByStr(str, type) {
    var oDate = new Date(str)
    oYear = oDate.getFullYear()
    oMonth = oDate.getMonth() + 1
    oDay = oDate.getDate()
    oHour = oDate.getHours()
    oMin = oDate.getMinutes()
    oSen = oDate.getSeconds()

    if (type == 'datetime')
        oTime = oYear + '-' + p(oMonth) + '-' + p(oDay) + ' ' + p(oHour) + ':' + p(oMin) + ':' + p(oSen)
    else if (type === 'time')
        oTime = p(oMin) + ':' + p(oSen)
    else
        oTime = oYear + '-' + p(oMonth) + '-' + p(oDay)
    return oTime;
}

export function statusById(index) {
    const status = {
        '10': '',
        '20': '',
        '90': ''
    }
    return status[index]
}

//0

function p(s) {
    return s < 10 ? '0' + s : s;
}



export function getMenuNameById(id) {
    let name = ''
    let obj = JSON.parse(localStorage.getItem("menuList"))
    if (obj) {
        for (let i = 0; i < obj.length; i++) {
            if (obj[i].dicNo === id) {
                name = obj[i].dicDesc
                break;
            }
        }
    }
    return name
}


